import { getCurrentTimeMills } from './getCurrentTimeMills';

/**
 * 判断当前时间在某一时间差内是否过期
 * @param timeStamp 过期的时间戳
 * @param timeDiff 时间戳间隔
 * @returns 是否过期
 */
export function isTimeExpired(timeStamp: number, timeDiff: number) {
    const currentTimeMills = getCurrentTimeMills();
    return currentTimeMills + timeDiff > timeStamp;
}
